import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Button, Form, Modal } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import axios from '../utils/axios';
import { useAuth } from '../context/AuthContext';

const TaskList = () => {
    const [tasks, setTasks] = useState([]);
    const [newTask, setNewTask] = useState('');
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [editingTask, setEditingTask] = useState(null);
    const [showEditModal, setShowEditModal] = useState(false);
    const navigate = useNavigate();
    const { logout } = useAuth();

    const handleLogout = () => {
        // Update the auth context first to clear user state
        logout();
        
        // Clear local storage
        localStorage.removeItem('token');
        localStorage.removeItem('user');
        
        // Then navigate immediately
        navigate('/login', { replace: true });
    };

    useEffect(() => {
        fetchTasks();
    }, []);

    const fetchTasks = async () => {
        try {
            setLoading(true);
            console.log('Fetching tasks...');
            const response = await axios.get('/tasks');
            console.log('Tasks response:', response.data);
            setTasks(response.data);
            setError(null);
        } catch (err) {
            console.error('Error fetching tasks:', err);
            setError(err.response?.data?.message || 'Failed to fetch tasks. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!newTask.trim()) return;

        try {
            const taskData = {
                title: newTask,
                description: '',
                completed: false,
                status: 'Pending'
            };
            
            console.log('Creating task:', taskData);
            const response = await axios.post('/tasks', taskData);
            console.log('Create task response:', response.data);
            setTasks([...tasks, response.data]);
            setNewTask('');
            setError(null);
        } catch (err) {
            console.error('Error creating task:', err);
            setError(err.response?.data?.message || 'Failed to create task. Please try again.');
        }
    };

    const handleDelete = async (taskId) => {
        try {
            console.log('Deleting task:', taskId);
            await axios.delete(`/tasks/${taskId}`);
            setTasks(tasks.filter(task => task.id !== taskId));
            setError(null);
        } catch (err) {
            console.error('Error deleting task:', err);
            setError(err.response?.data?.message || 'Failed to delete task. Please try again.');
        }
    };

    const handleToggleComplete = async (taskId, completed) => {
        try {
            const task = tasks.find(t => t.id === taskId);
            const updatedTask = {
                ...task,
                completed: !completed,
                status: !completed ? 'Completed' : 'Pending'
            };
            
            console.log('Updating task:', updatedTask);
            const response = await axios.put(`/tasks/${taskId}`, updatedTask);
            console.log('Update task response:', response.data);
            setTasks(tasks.map(task => 
                task.id === taskId ? response.data : task
            ));
            setError(null);
        } catch (err) {
            console.error('Error updating task:', err);
            setError(err.response?.data?.message || 'Failed to update task. Please try again.');
        }
    };

    const handleEdit = (task) => {
        setEditingTask({
            ...task,
            due_date: task.dueDate ? new Date(task.dueDate).toISOString().split('T')[0] : ''
        });
        setShowEditModal(true);
    };

    const handleUpdateTask = async (e) => {
        e.preventDefault();
        try {
            const taskToUpdate = {
                ...editingTask,
                dueDate: editingTask.due_date || null
            };
            delete taskToUpdate.due_date; // Remove the temporary field

            console.log('Updating task:', taskToUpdate);
            const response = await axios.put(`/tasks/${editingTask.id}`, taskToUpdate);
            console.log('Update task response:', response.data);
            setTasks(tasks.map(task => 
                task.id === editingTask.id ? response.data : task
            ));
            setShowEditModal(false);
            setEditingTask(null);
            setError(null);
        } catch (err) {
            console.error('Error updating task:', err);
            setError(err.response?.data?.message || 'Failed to update task. Please try again.');
        }
    };

    const formatDate = (dateString) => {
        if (!dateString) return '';
        const date = new Date(dateString);
        return date.toLocaleDateString();
    };

    if (loading) {
        return <div className="text-center">Loading...</div>;
    }

    return (
        <Container className="pt-2">
            <h2 className="mb-3">Tasks</h2>
            {error && (
                <div className="alert alert-danger" role="alert">
                    {error}
                </div>
            )}
            <Row className="justify-content-center mb-3">
                <Col md={8}>
                    <Card>
                        <Card.Body>
                            <Card.Title>Add New Task</Card.Title>
                            <Form onSubmit={handleSubmit}>
                                <Form.Group className="mb-3">
                                    <Form.Control
                                        type="text"
                                        value={newTask}
                                        onChange={(e) => setNewTask(e.target.value)}
                                        placeholder="Add a new task"
                                        required
                                    />
                                </Form.Group>
                                <Button type="submit" variant="primary">
                                    Add
                                </Button>
                            </Form>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>

            <Row>
                {tasks.length === 0 ? (
                    <Col className="text-center text-muted">
                        No tasks yet. Add one above!
                    </Col>
                ) : (
                    tasks.map(task => (
                        <Col key={task.id} md={4} className="mb-3">
                            <Card className={`h-100 ${task.completed ? 'bg-light' : ''}`}>
                                <Card.Body>
                                    <div className="d-flex justify-content-between align-items-start mb-2">
                                        <div className="form-check">
                                            <input
                                                type="checkbox"
                                                className="form-check-input"
                                                checked={task.completed}
                                                onChange={() => handleToggleComplete(task.id, task.completed)}
                                            />
                                        </div>
                                        <span className={`badge ${task.status === 'Completed' ? 'bg-success' : 'bg-warning'}`}>
                                            {task.status}
                                        </span>
                                    </div>
                                    <Card.Title className={`mb-3 ${task.completed ? 'text-decoration-line-through text-muted' : ''}`}>
                                        {task.title}
                                    </Card.Title>
                                    {task.description && (
                                        <Card.Text className="text-muted">
                                            {task.description}
                                        </Card.Text>
                                    )}
                                    {task.dueDate && (
                                        <Card.Text className="text-muted">
                                            Due: {formatDate(task.dueDate)}
                                        </Card.Text>
                                    )}
                                    <div className="d-flex justify-content-end mt-3">
                                        <Button
                                            variant="outline-primary"
                                            size="sm"
                                            className="me-2"
                                            onClick={() => handleEdit(task)}
                                        >
                                            Edit
                                        </Button>
                                        <Button
                                            variant="outline-danger"
                                            size="sm"
                                            onClick={() => handleDelete(task.id)}
                                        >
                                            Delete
                                        </Button>
                                    </div>
                                </Card.Body>
                            </Card>
                        </Col>
                    ))
                )}
            </Row>

            {/* Edit Task Modal */}
            <Modal show={showEditModal} onHide={() => setShowEditModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Edit Task</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={handleUpdateTask}>
                        <Form.Group className="mb-3">
                            <Form.Label>Title</Form.Label>
                            <Form.Control
                                type="text"
                                value={editingTask?.title || ''}
                                onChange={(e) => setEditingTask({...editingTask, title: e.target.value})}
                                required
                            />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Description</Form.Label>
                            <Form.Control
                                as="textarea"
                                rows={3}
                                value={editingTask?.description || ''}
                                onChange={(e) => setEditingTask({...editingTask, description: e.target.value})}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Due Date</Form.Label>
                            <Form.Control
                                type="date"
                                value={editingTask?.due_date || ''}
                                onChange={(e) => setEditingTask({...editingTask, due_date: e.target.value})}
                            />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label>Status</Form.Label>
                            <Form.Select
                                value={editingTask?.status || 'Pending'}
                                onChange={(e) => setEditingTask({...editingTask, status: e.target.value})}
                            >
                                <option value="Pending">Pending</option>
                                <option value="Completed">Completed</option>
                            </Form.Select>
                        </Form.Group>
                        <div className="d-flex justify-content-end">
                            <Button variant="secondary" className="me-2" onClick={() => setShowEditModal(false)}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Save Changes
                            </Button>
                        </div>
                    </Form>
                </Modal.Body>
            </Modal>
        </Container>
    );
};

export default TaskList; 