import React, { useEffect, useState } from 'react';
import SockJS from 'sockjs-client';
import { Client } from '@stomp/stompjs';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './NotificationCenter.css';

const NotificationCenter = ({ userId }) => {
    const [notifications, setNotifications] = useState([]);
    const [stompClient, setStompClient] = useState(null);

    useEffect(() => {
        const socket = new SockJS('/ws');
        const client = new Client({
            webSocketFactory: () => socket,
            onConnect: () => {
                console.log('Connected to WebSocket');
                client.subscribe(`/user/${userId}/notifications`, onNotificationReceived);
            },
            onDisconnect: () => {
                console.log('Disconnected from WebSocket');
            }
        });

        client.activate();
        setStompClient(client);

        return () => {
            if (client) {
                client.deactivate();
            }
        };
    }, [userId]);

    const onNotificationReceived = (message) => {
        const notification = JSON.parse(message.body);
        setNotifications(prev => [notification, ...prev].slice(0, 10));
        
        // Show toast notification
        toast(notification.message, {
            type: getToastType(notification.type),
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            style: {
                marginTop: '60px' // Add margin to position below navbar
            }
        });
    };

    const getToastType = (notificationType) => {
        switch (notificationType) {
            case 'TASK_ASSIGNED':
                return 'info';
            case 'STATUS_CHANGED':
                return 'success';
            case 'DEADLINE_APPROACHING':
                return 'warning';
            case 'COMMENT_ADDED':
                return 'info';
            default:
                return 'info';
        }
    };

    const formatTime = (timestamp) => {
        return new Date(timestamp).toLocaleTimeString();
    };

    return (
        <div className="notification-center">
            <ToastContainer
                position="top-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                style={{ marginTop: '60px' }}
            />
            <h3>Notifications</h3>
            <div className="notification-list">
                {notifications.map((notification, index) => (
                    <div key={index} className={`notification-item ${notification.type.toLowerCase()}`}>
                        <div className="notification-message">{notification.message}</div>
                        <div className="notification-time">{formatTime(notification.timestamp)}</div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default NotificationCenter; 